<template>
  <div class="portfolio">
      <navigation />
      <div class="content-container">
            <div class="title">
                <h1>Featured Projects</h1>
            </div>
            <div class="projects">
                <div class="project" v-for="item in portfolio" :key="item.id">
                    <div class="image-container" >
                        <img v-if="item.img" :src="item.img" :alt="item.name">
                    </div>
                    <div class="description">
                        <a :href="item.url" target="_blank">{{item.name}}</a>
                        <img v-if="item.git" :src="gitub" alt="github">
                        <p v-html="item.description"></p>
                    </div>
                </div>
            </div>
      </div>
  </div>
</template>

<script>
import navigation from '@/components/navigation'

export default {
  name: 'Portfolio',
  components: {navigation},
  data () {
    return {
      portfolio: [
        {
          name: 'Asia Miles - Website Revamp',
          description: 'A leading Travel & Lifestyle Rewards website. <br/> Technologies Including: React.js, Redux, Sass, ES6, Axios',
          img: require('@/assets/image/noImg.png'),
          url: 'https://www.asiamiles.com/',
          git: ''
        },
        {
          name: 'MemoUD',
          description: 'A Calendar ReactJS application which allow you to add memos for your day.',
          img: require('@/assets/image/portfolio/memoUD.jpg'),
          url: 'http://memoud.herokuapp.com/',
          git: 'https://github.com/lucasckw2022/memoUD'
        }
      ],
      gitub: require('@/assets/image/github.png')
    }
  }
}
</script>



