<template>
  <div class="about-me">
      <navigation />
      <div class="content-container">
            <div class="title">
                <h1>Lucas Cheung</h1>
                <a href="https://ca.linkedin.com/in/lucasckw" target="_blank"><img :src="linkedin" alt="linkedin"></a>
                <a href="https://github.com/lucasckw2022" target="_blank"><img :src="gitub" alt="github"></a>
            </div>
            <div class="description">
                    <p>A enthusiastic person who likes spending time working and learning. Strong in Front-end side of programming and simply just like coding! </p>
            </div>
            <div class="stacks">
                <p>Highlighted Stack Include:</p>
                <div class="stack" v-for="item in stacks" :key="item.id">
                    <div class="icon">
                        <img :src="item.img" alt="item.name">
                    </div>
                    <div>{{item.name}}</div>
                </div>
            </div>
      </div>
  </div>
</template>

<script>
import navigation from '@/components/navigation'

export default {
  name: 'AboutMe',
  components: {navigation},
  data () {
    return {
      stacks: [
        {
            name: 'React',
            img: require('@/assets/image/stack/react.png')
        },
        {
            name: 'Vue',
            img: require('@/assets/image/stack/vue.png')
        },
        {
            name: 'Angular',
            img: require('@/assets/image/stack/angular.png')
        },
        {
            name: 'Redux',
            img: require('@/assets/image/stack/redux.png')
        },
        {
            name: 'Javascript',
            img: require('@/assets/image/stack/js.png')
        },
        {
            name: 'JQuery',
            img: require('@/assets/image/stack/jquery.png')
        },
        {
            name: 'HTML',
            img: require('@/assets/image/stack/html.png')
        },
        {
            name: 'CSS',
            img: require('@/assets/image/stack/css.png')
        },
        {
            name: 'SCSS',
            img: require('@/assets/image/stack/sass.png')
        },
      ],
      gitub: require('@/assets/image/github.png'),
      linkedin: require('@/assets/image/linkedin.png'),
    }
  }
}
</script>



